import React, { Component } from "react";
import userData from "./api/userData";

class App extends Component {
  constructor() {
    super();
    this.state = {
      img: null,
      error: false,
      loading: false
    };
  }
  handleSubmit = e => {
    e.preventDefault();
    this.setState({ loading: true });
    userData(this.refs.username.value)
      .then(data => {
        console.log(data);
        this.setState({
          img: data.hd_profile_pic_url_info.url,
          error: false,
          loading: false
        });
      })
      .catch(err => {
        console.log("from app.js", err);
        this.setState({ error: true, loading: false, img:null });
      });
  };

  render() {
    let { loading, error, img } = this.state;
    return (
      <div className="container">
        <div className="flex">
          <div className="box">
            <h3>Hey, Stalker!</h3>
            <h2>ğŸ“¸ Instagram DP</h2>
            <div className="form-container">
              <form onSubmit={this.handleSubmit}>
                <input ref="username" placeholder="Enter username âœï¸" />
                <button type="submit">
                  ğŸ”<span>Search</span>
                </button>
              </form>
            </div>
            <div className="dynamic">
              {loading ? (<div><p>â³ Loading...</p></div>) : error ? (<div><p>ğŸ˜¢ User Not Found</p></div>) : img ? (
                
                  <div><p>We found it! ğŸ˜</p><a target={"_blank"} href={img}><div><img alt={"instagram dp"} src={img} /></div>ğŸ™„ View In full size</a></div>) : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
